@import 'fonts'; // Шрифты
@import 'reset'; // Сброс стилей
@import 'setting'; // Общие настройки
@import 'vars'; // Переменные
@import 'mixins'; // Миксины


// Компоненты 
@import 'components/footer';
@import 'components/header';
  



// Стили
.burger{
    cursor: pointer;
}
@media(max-width: 991px){
    .mobile-hidden{
        display: none !important;
    }
}

@media(min-width: 991px){
    .desktop-hidden{
        display: none;
    }
}
.burger{
    @media(min-width: 991px){
        display: none;
    }
}
.header{
    padding-top: 23px;
    @media(max-width: 991px){
        padding-top: 15px;
    }
    &__line{
        display: flex;
        align-items: center;
        padding-bottom: 15px;
        @media(max-width: 991px){
            justify-content: space-between;
        }
    }

    &__logo{

        &-link{
            margin-right: 28px;
        }
        &-img{
            max-width: 279px;
            @media(max-width: 991px){
                max-width: 180px;
            }
        }
    }

    .user-info{
        display: flex;
        flex-direction: column;
        margin-top: -3px;
        @media(max-width: 991px){
            flex-direction: row;
            justify-content: space-between;
        }
        span{
            font-weight: 400;
            font-size: 14px;
            line-height: 120%;
            b{
                font-weight: 700;
            }
            a{
                text-decoration: none;
                color: #0061A3;
                font-weight: 500;
                font-size: 14px;
                line-height: 120%;
            }
        }
    }

    &__box{
        &--right{
            margin-left: auto;
            margin-right: 35px;
            @media(max-width: 991px){
                display: none;
            }
        }
    }


    &__social-links{
        display: grid;
        grid-template-columns: repeat(4, 16px);
        grid-template-rows: 16px;
        grid-gap: 10px;
        margin-bottom: 10px;
        @media(max-width: 991px){
            display: none;
        }
    }

    .language-select{
        display: flex;
        align-items: center;
        position: relative;
        @media(max-width: 991px){
            display: none;
        }
        &:before{
            content: '';
            position: absolute;
            background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.88 9.29006L12 13.1701L8.11998 9.29006C7.72998 8.90006 7.09998 8.90006 6.70998 9.29006C6.31998 9.68006 6.31998 10.3101 6.70998 10.7001L11.3 15.2901C11.69 15.6801 12.32 15.6801 12.71 15.2901L17.3 10.7001C17.69 10.3101 17.69 9.68006 17.3 9.29006C16.91 8.91006 16.27 8.90006 15.88 9.29006Z' fill='black'/%3E%3C/svg%3E%0A");
            width: 24px;
            height: 24px;
            background-size: cover;
            background-repeat: no-repeat;
            right: -15px;
            top: 50%;
            transform: translateY(-50%);
        }
        img{
            margin-right: 5px;
        }
        select{
            font-weight: 400;
            font-size: 14px;
            line-height: 150%;
            cursor: pointer;
            width: 100%;
        }
    }

    &__menu{
        display: flex;
        align-items: center;
        background: #F30000;
        border-radius: 8px;
        height: 34px;
        display: none;
        &.open{
            display: block !important;
        }
        .close{
            position: absolute;
            top: 15px;
            right: 15px;
            z-index: 99;
        }
        @media(max-width: 991px){
            padding: 50px 20px;
            flex-direction: column;
            width: 100%;
            height: 100%;
            position: fixed;
            top: 0;
            left: 0;
            border-radius: 0;
            background: #fff;
        }
        ul{
            margin-right: auto;
            display: flex;
            align-items: center;
            @media(max-width: 991px){
                flex-direction: column;
                margin-right: 0;
                
            }
            li{
                border-right: 1px solid #fff;
                @media(max-width: 991px){
                    border-right: 0;
                    margin-bottom: 15px;
                }
            }
            li a{
                padding: 2.5px 25px;
                font-weight: 700;
                font-size: 16px;
                line-height: 120%;
                text-transform: uppercase;
                color: #fff;
                &:hover{
                    text-decoration: underline;
                }
                @media(max-width: 991px){
                    color: #000;
                }
            }

            a.active{
                text-decoration: underline;
            }
        }

        .profile-link{
            padding: 7.5px 38px;
            background: #0061A3;
            border-radius: 8px;
            color: #fff;
            font-weight: 700;
            font-size: 16px;
            line-height: 120%;
            text-transform: uppercase;
            &:hover{
                text-decoration: underline;
            }
        }
    }

    
}






.banner{
    padding: 30px 0px;
    img{
        width: 100%;
        max-height: 462px;
        height: auto;
    }
    @media(max-width: 768px){
        padding-bottom: 16px;
    }
}

.bordered{
    border: 1px solid #E0E0E0;
    border-radius: 8px;
}

.col-3{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(343px, 1fr));
    grid-template-rows: auto;
    grid-gap: 15px;
}

.cards{
   
    &__item{
        display: flex;
        align-items: center;
        padding: 10px 5px;
        padding-right: 15px;
        img{
            margin-right: 5px;
        }
        &-content{
            min-height: 80px;
            width: 100%;
        }
        &-title{
            display: flex;
            align-items: center;
            justify-content: space-between;
            flex-wrap: wrap;
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            text-transform: uppercase;
            margin-bottom: 5px;
            span{
                display: block;
                margin-right: 10px;
            }
        }
        &-link{
            font-weight: 500;
            font-size: 14px;
            line-height: 21px;
            text-decoration-line: underline;
            color: #0061A3;
            text-transform: none;
        }

        &-text{
            font-weight: 400;
            font-size: 14px;
            line-height: 120%;
            color: #5F5F5F;
            padding-right: 20px;
        }
    }
}


.lotteries{
    margin-top: 75px;
    @media(max-width: 768px){
        margin-top: 35px;
    }
    &__title{
        margin-bottom: 30px;
        font-weight: 500;
        font-size: 24px;
        line-height: 36px;
        @media(max-width: 768px){
            font-size: 18px;
            margin-bottom: 18px;
        }
    }
    &__body{
        display: flex;
        align-items: center;
        padding: 5px 28px;
        img{
            margin-right: 23px;
            @media(max-width: 768px){
                max-width: 127px;
                margin-right: 30px;
            }
        }
    }

   

    &__footer{
        background: #0061A3;
        padding-top: 13px;
        padding-left: 12px;
        padding-bottom: 8px;
        padding-right: 12px;
        display: flex;
        align-items: center;
        border-bottom-right-radius: 8px;
        border-bottom-left-radius: 8px;
        

       
    }
}

.lotteries-link{
    padding: 6px 51px;
    background: #F7D300;
    border-radius: 8px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: #000000;
    margin-right: 40px;
    display: inline-block;
    transition: 0.2s linear;
    cursor: pointer;
    &:hover{
        background: darken(#F7D300, 5%);
    }
}


.lotteries-price{
    font-weight: 700;
    font-size: 26px;
    line-height: 22px;
    color: #13902A;
    @media(max-width: 768px){
        font-size: 24px;
    }
}


.timer-wrap{
    display: flex;
    align-items: center;
    color: #fff;
   svg{
    margin-right: 4px;
   }
}


.lotteries-type{
    display: flex;
    align-items: center;
    margin-bottom: 10px;
    text-transform: uppercase;
    img{
        margin-right: 10px;
        
    }
    @media(max-width: 768px){
        font-size: 14px;
    }
}

.red-title{
    width: 100%;
    text-align: center;
    padding: 10px;
    background: #F30000;
    border-radius: 8px;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: #FFFFFF;
    margin-bottom: 15px;
    @media(max-width: 768px){
        font-size: 16px;
     }
}

.online-lotteries{
    margin-top: 74px;
    @media(max-width: 768px){
       margin-top: 32px;
     }
    &__grid{
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(180px, 208px));
        grid-gap: 15px;
        justify-content: center;
        @media(max-width: 540px){
            grid-template-columns: repeat(2, 1fr);
        }
    }

    &__item{
        display: flex;
        flex-direction: column;
        position: relative;
        align-items: center;
        text-align: center;
        padding: 13px;
        @media(max-width: 768px){
           padding: 8px;
        }
        .favorite-btn{
            position: absolute;
            top: 10px;
            right: 10px;
            cursor: pointer;
        }
        &-img{
            border: 2px solid #E0E0E0;
            border-radius: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-bottom: 28px;
            @media(max-width: 768px){
                width: 81px;
                height: 78px;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        .lotteries-type{
            font-size: 14px;
            margin-bottom: 8px;
            @media(max-width: 768px){
                font-size: 12px;
            }
        }

        &-price{
            font-weight: 700;
            font-size: 20px;
            line-height: 22px;
            color: #000000;
            margin-bottom: 8px;
            @media(max-width: 768px){
                font-size: 18px;
            }
        }

        .timer-wrap{
            margin-bottom: 12px;
            @media(max-width: 768px){
                margin-bottom: 8px;
            }
            .timer{
                color: #0061A3;
                @media(max-width: 768px){
                   font-size: 14px;
                }
            }
        }

        .lotteries-link{
            padding-left: 0;
            padding-right: 0;
            width: 100%;
            margin-right: 0;
        }
    }


}


.more-link{
    display: block;
    width: max-content;
    margin-left: auto;
    margin-top: 15px;
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    text-decoration-line: underline;
    color: #0061A3;
    padding-right: 50px;
    @media(max-width: 540px){
       padding-right: 0;
       font-size: 16px;
    }
}


.winners__inner{
    display: grid;
    grid-template-columns: minmax(50%, 775px) 380px;
    grid-gap: 15px;
    margin-top: 75px;
    @media(max-width: 768px){
        margin-top: 32px;
    }
    @media(max-width: 991px){
        grid-template-columns: 1fr;
    }
    .winners__col:first-child .winners__box{
        padding: 20px;
        @media(max-width: 768px){
           padding: 15px;
        }
    }

    .winners__box-inner{
        margin-top: 25px;
        display: flex;
        @media(max-width: 768px){
            flex-direction: column;
            align-items: center;
        }
        .winner-img{
            display: flex;
            flex-direction: column;
            width: 60%;
            min-width: 280px;
            @media(max-width: 768px){
                width: 100%;
            }
            img{
                margin-right: 15px;
                width: 100%;
            }
        }

        .loto-result__wrapper{
            display: flex;
            justify-content: center;
            font-weight: 500;
            font-size: 14px;
            line-height: 120%;
            text-transform: uppercase;
            margin-top: 20px;
            @media(max-width: 768px){
                width: 100%;
            }
            .result{
                
                .numbers{
                    display: flex;
                    align-items: center;
                    margin-left: 10px;
                    .number{
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 120%;
                        text-align: center;
                        color: #FFFFFF;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        padding: 3px;
                        border-radius: 50px;
                        background: #F30000;
                        margin-right: 4px;
                    }
                }

                .date{
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 130%;
                    text-align: center;
                    margin-top: 5px;
                    text-transform: none;
                }
            }
        }

        .winners__info{
            @media(max-width: 768px){
                width: 100%;
                text-align: center;
                margin-top: 16px;
            }
           &-title{
            font-weight: 500;
            font-size: 16px;
            line-height: 24px;
            margin-bottom: 15px;
            display: block;
           }
           &-text{
            font-weight: 400;
            font-size: 14px;
            line-height: 21px;
            margin-bottom: 30px;
            display: block;
            max-width: 235px;
            @media(max-width: 768px){
                max-width: 100%;
                margin-bottom: 12px;
            }
           }
           a{
            font-weight: 500;
            font-size: 14px;
            line-height: 120%;
            text-decoration-line: underline;
            color: #0061A3;
            display: block;
            width: max-content;
            margin: 0 auto;
           
           }
           .play-link{
                display: flex;
                align-items: center;
                justify-content: center;
                text-align: center;
                background: #F7D300;
                border-radius: 8px;
                font-weight: 500;
                font-size: 14px;
                line-height: 130%;
                color: #000;
                text-decoration: none;
                width: 100%;
                height: 40px;
                margin-top: 50px;
                @media(max-width: 768px){
                    margin-top: 16px;
                }
           }
        }

        
    }
    .number-wrapper{
        display: flex;
        align-items: center;
        font-weight: 500;
        font-size: 28px;
        line-height: 36px;
        text-transform: uppercase;
        justify-content: center;
        @media(max-width: 768px){
            font-size: 27px;
        }
        .number{
            font-weight: 700;
            font-size: 36px;
            line-height: 22px;
            color: #0061A3;
            display: inline-block;
            margin-left: 10px;
            @media(max-width: 768px){
                font-size: 27px;
            }
        }
        img{
            margin-right: 20px;
            width: 50px;
            height: 50px;
        }
    }

    .total-numbers{
        font-weight: 400;
        font-size: 18px;
        line-height: 120%;
        color: #000000;
        text-align: center;
        padding: 5px;
        display: flex;
        flex-direction: column;
        gap: 5px;
        .green-number{
            font-weight: 700;
            font-size: 26px;
            line-height: 22px;
            color: #13902A;
        }
        .black-number b{
            font-weight: 500;
            font-size: 26px;
            line-height: 120%;
            display: inline-block;
            margin-right: 10px;
        }
    }

    .winner-list{
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        @media(max-width: 768px){
            margin-top: 16px;
        }
        .winner{
            margin-bottom: 10px;
            padding: 10px 15px;
            display: flex;
            align-items: center;
            &:last-child{
                margin-bottom: 0;
            }
            img{
                margin-right: 16px;
            }
            &-content{
                .date{
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 21px;
                }
                .name{
                    display: flex;
                    align-items: center;
                    img{
                        margin-right: 5px;
                    }
                }

                .text{
                    font-weight: 400;
                    font-size: 14px;
                    line-height: 21px;
                    text-align: center;
                    color: #5F5F5F;
                    letter-spacing: 1px
                }
            }
        }
    }
}

.questions{
    margin-top: 75px;
    @media(max-width: 768px){
        display: none;
    }
    &__list{
        max-width: 775px;
        width: 100%;
        padding-top: 30px;
        .question{
            border-bottom: 1px solid #E0E0E0;
            margin-bottom: 15px;
            &-title{
                width: 100%;
                font-weight: 500;
                font-size: 18px;
                line-height: 24px;
                color: #0061A3;
                padding: 10px 0px;
                position: relative;
                cursor: pointer;
                &::before{
                    content: '';
                    position: absolute;
                    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M15.88 9.29006L12 13.1701L8.11998 9.29006C7.72998 8.90006 7.09998 8.90006 6.70998 9.29006C6.31998 9.68006 6.31998 10.3101 6.70998 10.7001L11.3 15.2901C11.69 15.6801 12.32 15.6801 12.71 15.2901L17.3 10.7001C17.69 10.3101 17.69 9.68006 17.3 9.29006C16.91 8.91006 16.27 8.90006 15.88 9.29006Z' fill='black'/%3E%3C/svg%3E%0A");
                    width: 24px;
                    height: 24px;
                    background-size: cover;
                    background-repeat: no-repeat;
                    right: 0;
                    top: 20px;
                    transform: translateY(-50%);
                }
            }
           
            &-answer{
                padding: 10px 0px;
                font-size: 16px;
                font-weight: 400;
                display: none;
                
            }

            &.opened{
                .question-answer{
                    display: block;
                }
                .question-title{
                    &::before{
                        background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.40148 14.7099L12.2127 10.8299L16.024 14.7099C16.407 15.0999 17.0259 15.0999 17.409 14.7099C17.7921 14.3199 17.7921 13.6899 17.409 13.2999L12.9003 8.70994C12.5172 8.31994 11.8984 8.31994 11.5153 8.70994L7.00665 13.2999C6.62356 13.6899 6.62356 14.3199 7.00664 14.7099C7.38973 15.0899 8.01839 15.0999 8.40148 14.7099V14.7099Z' fill='%235F5F5F'/%3E%3C/svg%3E%0A");
                    }
                }
            }

        
        }
    }
}


.footer{
    margin-top: 170px;
    @media(max-width: 768px){
        margin-top: 35px;
    }
    &__inner{
        background-image: url('../img/footer-bg.svg');
        @media(max-width: 768px){
            background-image: url('../img/footer-mobile.svg');
        }
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        padding: 30px 100px;
        @media(max-width: 991px){
            padding: 30px 0px;
            padding-bottom: 0;
        }
    }

    &__contact{
        display: flex;
        align-items: center;
        justify-content: space-between;
        .whatsapp{
            display: flex;
            img{
                margin-right: 15px;
            }
            &-text{
                display: flex;
                flex-direction: column;
                .big{
                    font-weight: 500;
                    font-size: 16px;
                    line-height: 21px;
                    color: #000000;
                    display: inline-block;
                }
                span{
                    font-weight: 400;
                    font-size: 12px;
                    line-height: 21px;
                    color: #5F5F5F;
                }
            }
        }

        .social{
            margin-right: 24px;
            @media(max-width: 768px){
               display: none;
            }
            a{
                margin-left: 10px;
            }
        }
    }

    &__cols{
        display: flex;
        justify-content: space-between;
        margin-top: 35px;
        @media(max-width: 768px){
            margin-top: 20px;
         }
        @media(max-width: 768px){
            flex-direction: column;
        }
        .footer__col{
            max-width: 250px;
            &:last-child{
                @media(max-width: 768px){
                    display: flex;
                    gap: 30px;
                }
            }
            @media(max-width: 768px){
                max-width: 100%;
                margin-bottom: 20px;
            }
            .payments{
                display: grid;
                grid-template-columns: repeat(3, 44px);
                grid-gap: 10px;
                margin-bottom: 30px;
            }
            .col-title{
                font-weight: 500;
                font-size: 16px;
                line-height: 21px;
                text-transform: uppercase;
                color: #000000;
                margin-bottom: 15px;
                
            }

            .footer-text{
                font-weight: 400;
                font-size: 12px;
                line-height: 120%;
                margin-bottom: 5px;
                &--medium{
                    font-weight: 400;
                    font-size: 13px;
                    line-height: 21px;
                }
            }
            ul{
                @media(max-width: 768px){
                   display: grid;
                   grid-template-columns: 160px 130px;
                    grid-column-gap: 15px;
                    grid-row-gap: 5px;
                }
                li{
                    margin-bottom: 5px;
                    @media(max-width: 991px){
                       margin-bottom: 0;
                    }
                    a{
                        font-weight: 400;
                        font-size: 16px;
                        line-height: 21px;
                        color: #000000;

                        @media(max-width: 768px){
                           font-size: 13px;
                        }

                        
                    }
                }
            }
        }
    }
}
@import 'media';  // Должен быть в конце проекта